.sky {
    position: absolute; top:0;
    height:100%;
    width:100%;
    z-index: 0;
    animation-name: sky_anim;
    animation-iteration-count: 1;
    animation-duration: 2s;
}
@keyframes sky_anim {
    0% { transform: translateY(-2%); }
    100% { transform: translateY(0%); }
}
.ground {
    position: absolute; bottom:0;
    height:20%;
    width:100%;
    z-index: 5;
}
.mntn1 {
    position: absolute;
    background-size: contain;
    bottom: -75%;
    left:-20%;
    width:150%; height:150%;
    z-index: 0;
    animation-name:raise1;
    animation-duration: 2s;
    animation-iteration-count: 1;
}
@keyframes raise1{
    0% { transform: translateY(2%); }
    100% { transform: translateY(0); }
}
.mntn2 {
    position: absolute;
    bottom: -80%;
    left: 0;
    background-size: contain;
    background-position-x: left;
    width:150%; height:150%;
    z-index: 1;
    animation-name:raise2;
    animation-duration: 2s;
    animation-iteration-count: 1;
}
@keyframes raise2{
    0% { transform: translateY(4%); }
    100% { transform: translateY(0); }
}
.mntn3 {
    position: absolute; bottom:-58%;
    background-size: contain;
    width:150%; height:150%;
    z-index: 2;
    animation-name:raise3;
    animation-duration: 2s;
    animation-iteration-count: 1;
}
@keyframes raise3{
    0% { transform: translateY(6%); }
    100% { transform: translateY(0); }
}
#moon {
    position: absolute;
    top: 6%;
    left: 10%;
    background-size: contain;
    width: 8vh;
    height: 8vh;
    z-index: 5;
    animation-name: moon_anim;
    animation-iteration-count: 1;
    animation-duration: 2s;
}
@keyframes moon_anim {
    0% { transform: translate(30%, -15%); }
    100% { transform: translate(0%, 0%); }
}
#hut {
    position: absolute;
    left: 32%;
    bottom: 5%;
    width: 36%;
    height: 45%;
    background-size: contain;
    z-index: 6;
}
#hut-light {
    animation-name: blinking;
    animation-iteration-count: 3;
    animation-duration: 2s;
    animation-timing-function: linear;
}
#main-title {
    position: absolute;
    top: 27%;
    color: white;
    opacity: .95;
    left: 10%;
    z-index: 5;
    animation-name: title_anim;
    animation-iteration-count: 1;
    animation-duration: 2s;
}
@keyframes title_anim {
    0% { transform: translateX(-4%); }
    100% { transform: translateX(0); }
}
#title {
    font-size: 8vh;
}
#subtitle {
    font-size: 4vh;
}

#note {
    position: fixed;
    font-size: 1.5vh;
    bottom: 1%;
    right: 2%;
    z-index: 10;
    color: white;
    opacity: .5;
}

@keyframes blinking{
    0% { opacity:1 }

    39% { opacity:1 }
    40% { opacity:0 }
    43% { opacity:0 }
    44% { opacity:1 }

    100% { opacity:1 }
}

@media screen and (max-width: 850px) {
    .mntn1 { bottom: -71% }
    .mntn2 { bottom: -76% }
    .mntn3 { bottom: -60% }
    #hut {
        bottom:2%;
        width: 40%;
        left: 30%;
    }
    #title {
        font-size: 8vh;
    }
    #subtitle {
        font-size: 4vh;
    }
    .under-dev {
        font-size: 2vh;
    }
}
@media screen and (max-width: 600px) {
    #hut {
        bottom: 1%;
        width: 44%;
        left: 26%;
    }
    .mntn1 { bottom: -66% }
    .mntn2 { bottom: -72% }
    .mntn3 { bottom: -57% }
}
@media screen and (max-width: 525px) {
    .mntn1 { bottom: -62% }
    .mntn2 { bottom: -68% }
    .mntn3 { bottom: -53% }
}
@media screen and (max-width: 450px) {
    .mntn1 { bottom: -54% }
    .mntn2 { bottom: -57% }
    .mntn3 { bottom: -49% }
    #hut {
        left: 15%;
        bottom: 2%;
        width: 70%;
    }
    #moon {
        width: 10vw;
        height: 10vw;
    }

    #main-title {
        top: 15%;
        left: 6%;
    }
    #title {
        font-size: 5vh;
    }
    #subtitle {
        font-size: 3vh;
        margin: 1vw;
    }
    .under-dev {
        font-size: 2vh;
    }
}
@media screen and (min-height: 1000px) {
    .mntn1 { bottom: -55% }
    .mntn2 { bottom: -64% }
    .mntn3 { bottom: -53% }
    #hut { bottom:0% }
}
@media screen and (min-height: 1200px) {
    .mntn1 { bottom: -53% }
    .mntn2 { bottom: -62% }
    .mntn3 { bottom: -49% }
}
@media screen and (min-width: 2400px) {
    #main-title {
        top: 16%;
    }
    #title {
        font-size: 8vh;
    }
    #subtitle {
        font-size: 4vh;
    }
}